.dashboard {
  width: 100%;
  height: 100vh;
  display: flex;
}
.welcome--nav {
  font-family: "Satisfy", cursive;
  font-weight: 300;
  color: #ddd;
  text-decoration: none;
  font-size: 1.6rem;
}
.dash--nav {
  flex: 0.15;
  line-height: 100%;
  background-color: rgb(38, 38, 83);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.dash--nav > * {
  margin-bottom: 100px;
  color: #e8e9eb;
}
.body--nav > * {
  font-weight: 300;
  text-decoration: none;
  font-size: 1.2rem;
}
.nav--menuicon {
  display: none !important;
}
.dash--body {
  flex: 0.85;
}
.body--nav {
  width: 100%;
  height: 8vh;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  background-color: rgb(238, 238, 247);
  padding: 5px 50px;
}
.body--questionsContainer {
  display: flex;
  flex-direction: column;
  padding: 30px;
}
.body--questionsBtn {
  width: 100%;
  display: flex;
  align-items: center;
  margin: 30px 0px 20px 0px;
  justify-content: space-between;
}
.body--questionsBtn > button {
  height: 40px;
  width: 150px;
  background-color: transparent;
  border: 1px solid rgb(38, 38, 83);
  cursor: pointer;
  margin-bottom: 30px;
  outline: none;
}
.body--questionsBtn > button:hover {
  background-color: rgb(74, 74, 136);
  color: #f2f0f1;
}
.body--question,
.body--answer {
  font-weight: 500;
  margin-bottom: 45px;
}
.body--answerBtn {
  width: 200px;
  height: 45px;
  margin: auto;
  background-color: rgb(234, 234, 236);
  box-shadow: 0px 0px 5px #ddd;
  cursor: pointer;
  border: none;
  border-radius: 5px;
  outline: none;
}

.hamburger--nav {
  display: none;
}
.nav--course {
  margin-bottom: 80px;
  color: #ddd;
  font-weight: 300;
  text-decoration: none;
  font-size: 1.2rem;
}
.nav--course:hover {
  color: #f2f4f9;
  font-weight: 500;
}
@media screen and (max-width: 1024px) {
  .dash--nav {
    flex: 0.2;
  }
  .dash--body {
    flex: 0.8;
  }
}
@media screen and (max-width: 768px) {
  .dash--nav {
    display: none;
  }
  .dash--body {
    flex: 1;
  }
  .nav--menuicon {
    display: block !important;
  }
  .body--nav {
    height: 10vh;
    border-bottom: 1px solid #ddd;
    padding: 5px 30px;
    position: sticky;
    top: 0px;
  }
  .body--navHome {
    display: none;
  }
  .hamburger--nav {
    display: flex;
    flex-direction: column;
    justify-content: center;
    height: 100vh;
    position: fixed;
    top: 0;
    width: 50%;
    background-color: rgb(38, 38, 83);
    align-items: center;
  }
  .nav--home {
    font-family: "Satisfy", cursive;
    font-weight: 300;
    color: #f2f4f9;
    margin-bottom: 80px;
    font-size: 1.6rem;
    text-decoration: none;
  }
}
